/* global CustomFunctions, AbortController */

import { arborAxiosLimiter } from "../http/limiter";
import { AxiosError } from "axios";
import { isVersionSupported, returnErrorEntity } from "./utils";
import { cellProvider, successCellIcon } from "../constants";

/**
 * Returns a list of answers from GPT.
 * @customfunction ARBOR_GPT_LIST
 * @param {string[]} query The question to ask that should return a list, containing multiple sentences
 * @param {CustomFunctions.CancelableInvocation} invocation Invocation object.
 * @returns {Promise<Excel.EntityCellValue | string>} The list answer from GPT
 * @cancellable
 */
export async function arborGptList(
  query: string[],
  invocation: CustomFunctions.CancelableInvocation
): Promise<Excel.EntityCellValue | string> {
  const diagnostic = Office.context.diagnostics;
  const isSupported = isVersionSupported(
    diagnostic.host as unknown as string,
    diagnostic.platform as unknown as string,
    diagnostic.version as unknown as string
  );

  const controller = new AbortController();

  invocation.onCanceled = () => {
    controller.abort();
  };

  const queryString = query.join(" ");

  if (!queryString.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
  }

  try {
    const response = await arborAxiosLimiter.post(
      `/api/v1/screener/gpt-list`,
      {
        query: queryString,
      },
      {
        signal: controller.signal,
        // @ts-ignore
        controller,
      }
    );

    if (response.data?.error) {
      throw new AxiosError(response.data.error, response.status.toString(), response.config, response.request);
    }

    if (!response.data?.result || !Array.isArray(response.data.result)) {
      throw new AxiosError("FailToAnswer", response.status.toString(), response.config, response.request);
    }

    const entity: Excel.EntityCellValue = {
      type: Excel.CellValueType.entity,
      text: `List: ${queryString}`,
      provider: cellProvider,
      properties: {
        type: {
          type: Excel.CellValueType.string,
          basicValue: "ARBOR_GPT_LIST",
        },
        success: {
          type: Excel.CellValueType.boolean,
          basicValue: true,
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: queryString,
        },
        answer: {
          type: Excel.CellValueType.array,
          elements: response.data.result.map((str: string) => [str]),
        },
        answeredAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].end.toISOString(),
        },
        queriedAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].start.toISOString(),
        },
      },
      layouts: {
        compact: {
          icon: successCellIcon,
        },
      },
    };
    console.log("ARBOR_GPT_LIST", queryString, entity);
    if (!isSupported) {
      return response.data.result.join("\n");
    }
    return entity;
  } catch (error) {
    const errorEntity = returnErrorEntity(error, {
      type: {
        type: Excel.CellValueType.string,
        basicValue: "ARBOR_GPT_LIST",
      },
      success: {
        type: Excel.CellValueType.boolean,
        basicValue: false,
      },
      query: {
        type: Excel.CellValueType.string,
        basicValue: queryString,
      },
    });
    console.log("ARBOR_GPT_LIST", queryString, errorEntity);
    if (!isSupported) {
      return errorEntity.text;
    }
    return errorEntity;
  }
}

CustomFunctions.associate("ARBOR_GPT_LIST", arborGptList);