/* global CustomFunctions, AbortController */
import { arborAxiosLimiter } from "../http/limiter";
import { ArborScreenFailResult, ArborScreenResult } from "./types";
import { AxiosError } from "axios";
import { returnErrorEntity } from "./utils";
import { cellProvider, successCellIcon } from "../constants";
import { isVersionSupported } from "./utils";
/**
 * Screens stocks based on given criteria.
 * @customfunction ARBOR_SCREEN
 * @param {string} query The screening criteria
 * @param {CustomFunctions.CancelableInvocation} invocation Invocation object.
 * @returns {Promise<Excel.EntityCellValue | string>} An array of stock symbols that match the criteria
 * @cancellable
 */
export async function screen(
  query: string,
  invocation: CustomFunctions.CancelableInvocation
): Promise<Excel.EntityCellValue | string> {
  const diagnostic = Office.context.diagnostics;
  const isSupported = isVersionSupported(
    diagnostic.host as unknown as string,
    diagnostic.platform as unknown as string,
    diagnostic.version as unknown as string
  );
  const controller = new AbortController();

  invocation.onCanceled = () => {
    controller.abort();
  };

  if (!query.trim()) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Query is required");
  }

  try {
    const response = await arborAxiosLimiter.post(
      `/api/v1/screener/stock-suggestions`,
      { query },
      {
        signal: controller.signal, // @ts-ignore
        controller,
      }
    );

    if (!response?.data?.result || !response?.data?.result?.stocks?.length) {
      throw new AxiosError(`NoResult`, "NoResult", response.config);
    }

    const entity = {
      type: Excel.CellValueType.entity,
      text: `Screen: ${query}`,
      provider: cellProvider,
      properties: {
        success: {
          type: Excel.CellValueType.boolean,
          basicValue: true,
        },
        type: {
          type: Excel.CellValueType.string,
          basicValue: "ARBOR_SCREEN",
        },
        query: {
          type: Excel.CellValueType.string,
          basicValue: query,
        },
        answer: {
          type: Excel.CellValueType.array,
          elements: response.data.result.stocks.map((i) => [i.symbol, i.name]),
        },
        answeredAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].end.toISOString(),
        },
        queriedAt: {
          type: Excel.CellValueType.string,
          basicValue: response.config["metadata"].start.toISOString(),
        },
        ...(response.data.result.references?.length > 0
          ? {
              references: {
                type: Excel.CellValueType.array,
                elements: response.data.result.references.map((ref) => [ref.url as string, ref.title as string]),
              },
            }
          : {}),
      },
      layouts: {
        compact: {
          icon: successCellIcon,
        },
      },
    } as ArborScreenResult;
    console.log("ARBOR_SCREEN", query, entity);
    if (!isSupported) {
      return response.data.result.map((i) => i.symbol).join("\n");
    }
    return entity;
  } catch (error: any | AxiosError) {
    const errorEntity = returnErrorEntity<ArborScreenFailResult>(error, {
      type: {
        type: Excel.CellValueType.string,
        basicValue: "ARBOR_SCREEN",
      },
      query: {
        type: Excel.CellValueType.string,
        basicValue: query || "",
      },
    });
    console.log("ARBOR_SCREEN", query, errorEntity);
    if (!isSupported) {
      return errorEntity.text;
    }
    return errorEntity;
  }
}

CustomFunctions.associate("ARBOR_SCREEN", screen);