import axios, { AxiosError, InternalAxiosRequestConfig } from "axios";
import { ARBOR_CHAT_API, getFromLocalStorage } from "../taskpane/taskpane";
import store from "../stores/store"; // eslint-disable-next-line no-undef

const arborAxiosLimiter = axios.create({
  baseURL: ARBOR_CHAT_API,
  timeout: 1000000,
  headers: {
    "Content-Type": "application/json",
  },
});
// Create interceptor to handle concurrent requests
arborAxiosLimiter.interceptors.request.use(function (config) {
  const apiKey = getFromLocalStorage("apiKey");
  const apiHostStr = getFromLocalStorage("apiHost");
  const overrideApiHost = apiHostStr && apiHostStr.startsWith("http") ? apiHostStr : "";
  const apiHost = overrideApiHost || ARBOR_CHAT_API;

  config.baseURL = apiHost;
  config.headers["X-API-KEY"] = apiKey;
  return new Promise((resolve) => {
    store.handleRequest((c) => {
      c["metadata"] = {
        start: new Date(),
      };
      resolve(c as InternalAxiosRequestConfig);
    }, config);
  });
});

arborAxiosLimiter.interceptors.response.use(
  function (response) {
    const metadata = response.config["metadata"];
    if (metadata) {
      const duration = new Date().getTime() - metadata.start.getTime();
      console.log(`Request took ${duration}ms`);
      response.config["metadata"].end = new Date();
    }
    store.handleRequestDone(response.config);
    return Promise.resolve(response);
  },
  function (error) {
    const metadata = error.config["metadata"];
    if (metadata) {
      const duration = new Date().getTime() - metadata.start.getTime();
      console.log(`Request took ${duration}ms`);
      error.config["metadata"].end = new Date();
    }
    store.handleRequestDone(error.config);
    if (error instanceof AxiosError) {
      console.error([error.message, error.config.url, error.config.baseURL, error.config.method, error.config.data]);
    } else {
      console.error({ error });
    }
    return Promise.reject(error);
  }
);

export { arborAxiosLimiter };
