/* global window */

import { AxiosRequestConfig } from "axios";
import { getFromLocalStorage } from "../taskpane/taskpane";

// store.ts
interface StoreState {
  pendingResolvers: { resolve: (_config: AxiosRequestConfig) => void; config: AxiosRequestConfig }[];
  concurrentCount: number;
  recevied: number;
  processed: number;
  concurrency: number;
}

interface RequestStoreType {
  getListeners: () => Set<() => void>;
  getState: () => StoreState;
  handleRequest: (_resolve: (_config: AxiosRequestConfig) => void, _config: AxiosRequestConfig) => void;
  handleRequestDone: (_config: AxiosRequestConfig) => void;
  abortAllRequests: () => void;
  setState: (_newState: StoreState | ((_prev?: StoreState) => StoreState)) => void;
  subscribe: (_listener: () => void) => () => void;
  notify: () => void;
}

const MAX_REQUESTS_COUNT = 5;
let state: StoreState = {
  pendingResolvers: [],
  concurrentCount: 0,
  recevied: 0,
  processed: 0,
  concurrency: parseInt(getFromLocalStorage("concurrency")) || MAX_REQUESTS_COUNT,
};
let listeners: Set<() => void> = new Set();

const store: RequestStoreType = {
  getListeners(): Set<() => void> {
    return listeners;
  },

  getState(): StoreState {
    return state;
  },

  handleRequest(resolve: (_config: AxiosRequestConfig) => void, config: AxiosRequestConfig): void {
    if (this.getState().concurrentCount >= this.getState().concurrency) {
      this.setState((prev) => ({
        ...prev,
        recevied: prev.recevied + 1,
        pendingResolvers: [...prev.pendingResolvers, { resolve, config }],
      }));
    } else {
      this.setState((prev) => ({ ...prev, recevied: prev.recevied + 1, concurrentCount: prev.concurrentCount + 1 }));
      resolve(config);
    }
  },

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  handleRequestDone(_completedConfig: AxiosRequestConfig): void {
    if (state.pendingResolvers.length > 0) {
      const { resolve, config } = state.pendingResolvers.shift();
      this.setState((prev) => ({
        ...prev,
        processed: prev.processed + 1,
        pendingResolvers: [...state.pendingResolvers],
      }));
      resolve(config);
    } else {
      this.setState((prev) => ({
        ...prev,
        processed: prev.processed + 1,
        concurrentCount: Math.max(0, prev.concurrentCount - 1),
      }));
    }
    this.notify();
  },

  abortAllRequests(): void {
    this.getState().pendingResolvers.forEach(({ config }) => {
      console.log({ config: config["controller"] });
      if (config["controller"]) {
        config["controller"].abort();
      }
    });
    // state.runningAbortControllers.filter((s) => !s.signal.aborted).forEach((s) => s.abort());
    // state.runningAbortControllers = [];
  },

  setState(newState: StoreState | ((_prev?: StoreState) => StoreState)): void {
    if (typeof newState === "function") {
      const temp = newState(state);
      state = temp;
    } else {
      state = newState;
    }
    this.notify();
  },

  subscribe(listener: () => void): () => void {
    listeners.add(listener);
    return () => listeners.delete(listener);
  },

  notify(): void {
    this.getListeners().forEach((listener) => listener());
  },
};

if (!window["store"]) {
  window["store"] = store;
}
const windowStore: RequestStoreType = window["store"];

export default windowStore;
